.reviews {

    padding-top: 115px;
    padding-bottom: 140px;

    &-text {
        font-size: 24px;
        line-height: 33px;
        font-weight: 900;
        color: $gray;
    }

    &-title {
        font-family: Merriweather, serif;
        font-size: 48px;
        line-height: 69px;
        color: #333333;
        margin-bottom: 65px;
    }

}

.slider-reviews {

    &__list {
        margin-bottom: 51px;
    }

    &__item {
        background-color: $gray2;
        padding: 19px 94px 40px;
    }

    &__text {
        font-weight: 300;
        font-size: 18px;
        line-height: 25px;
        color: $black;
        margin-top: 25px;
        margin-bottom: 51px;
    }

    &__content {
        display: flex;
        align-items: center;
    }

    &__image {
        border-radius: 50%;
        margin-right: 20px;
    }

    &__name {
        font-weight: 900;
        font-size: 15px;
        line-height: 25px;
        color: $black;
        position: relative;
        top: -13px;
        left: 14px;
    }

    &__control {
        align-items: flex-end;
    }

}

.dots {

    &--reviews {
        display: flex;
    }

    &-dot {

        &--bg {
            background-color: $blue;
        }

    }

}

.arrows {

    &--reviews {
        display: flex;
    }

    &__button {

        &--bg {
            background-color: $blue;
        }

    }

}

@media (max-width: 991px) {
    .reviews {
        padding-top: 50px;
        padding-bottom: 50px;

        &-text {
            margin-top: 0;
            margin-bottom: 10px;
            text-align: center;
        }

        &-title {
            font-size: 40px;
            margin-bottom: 30px;
            text-align: center;
        }

    }

    .slider {

        &-reviews__list {
            margin-bottom: 30px;
        }

        &-reviews__item {
            padding-left: 30px;
            padding-right: 30px;
        }

        &-reviews__text {
            margin-bottom: 30px;
        }

    }
}

@media (max-width: 575px) {

    .reviews {

        &-text {
            font-size: 18px;
        }

        &-title {
            font-size: 22px;
        }

    }

    .slider {

        &-reviews {

            &__item {
                padding-left: 20px;
                padding-right: 20px;
                padding-top: 20px;
                padding-bottom: 20px;
            }

            &__text {
                margin-top: 0;
            }

        }

    }

}