.services {
    background-color: $gray2;
    margin-top: 31px;
    padding-top: 139px;
    padding-bottom: 109px;

    &-text {
        color: $gray;
        font-weight: 900;
        font-size: 24px;
        line-height: 33px;  
        margin-top: 0;      
    }

    &-title {
        font-family: Merriweather, serif;
        font-weight: 900;
        font-size: 48px;
        line-height: 69px;
        margin-bottom: 86px;
    }

    &-list {
        flex-wrap: wrap;

        &__item {
            width: 350px;
            min-height: 360px;
            background: $black url('../img/service-img.jpg') left center no-repeat;
            background-size: cover;
            margin-bottom: 30px;
            position: relative;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: rgba(0, 95, 163, 0.65);
                opacity: 0;
                transition: opacity 0.4s;
            }
            
            &:hover {
                &::before {
                    opacity: 1;
                }
            }
        }

        &__title {
            max-width: 210px;
            position: absolute;
            z-index: 1;
            bottom: 17px;
            left: 45px;
        }

        &__link {
            color: $white;
            font-family: Lato, sans-serif;
            display: block;
            font-weight: 900;
            font-size: 24px;
            line-height: 30px
        }

    }
}

@media (max-width: 1199px) {
    .services {
        &-list {
            &__item {
                width: 32%;
            }
        }
    }
}

@media (max-width: 991px) {
    .services {
        padding-top: 50px;
        padding-bottom: 50px;

        &-text {
            margin-bottom: 10px;
            text-align: center;
        }

        &-title {
            margin-bottom: 30px;
            font-size: 40px;
            text-align: center;
        }

        &-list {
            max-width: 800px;
            margin: 0 auto;
            &__item {
                width: 48%;
                min-height: 300px;
            }
            &__title {
                left: 11%;
            }
        }
    }
}

@media (max-width: 575px) {
    .services {
        &-text {
            font-size: 18px;
        }

        &-title {
            font-size: 22px;
            line-height: 45px;
        }

        &-list {
            max-width: 100%;

            &__item {
                width: 100%;
                min-height: 200px;
            }
        }
    }
}