.header {
  padding-top: 5px;
}

.logo {
  margin-top: 12px;
}

.logo__text {
  font-size: 36px;
  line-height: 50px;
  font-family: 'Merriweather', serif;
  color: $blue;
  margin-left: 27px;
}

.contacts {
  display: flex;
  justify-content: center;
  margin-top: 4px;
  margin-bottom: 20px;

  &__item {
    margin-right: 48px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    background-repeat: no-repeat;
    background-position: 0 0;
    padding-left: 30px;
    padding-top: 3px;
    padding-bottom: 3px;
    color: $black;
    font-size: 13px;
    line-height: 17px;

    &--phone {
    background-image: url('../img/phone.svg');

      &:hover {
        text-decoration: underline;
      }

    }

    &--clock {
    background-image: url('../img/clock.svg');
    }

    &--email {
    background-image: url('../img/message.svg');
    background-position-y: 2px;

      &:hover {
        text-decoration: underline;
      }
      
    }

    &--address {
    background-image: url('../img/geo.svg');
    }
  }
}

.menu {
  &__link {
    font-size: 14px;
    text-transform: uppercase;
    color: $black2;
    font-weight: 700;
    transition: color 0.5s;

    &:hover {
      color: $blue;
    }
  }
}

@media (max-width: 1199px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .header {
    .flex {
      flex-direction: column;
    }
  }

  .logo {
    align-self: center;
  }

  .nav {
    .flex {
      flex-direction: row;
    }
  }
}

@media (max-width: 767px) {
  .contacts {
    flex-wrap: wrap;
    max-width: 800px;
    margin: 0 auto;

    &__item {
      width: 30%;
      margin-right: 0;
      margin-bottom: 10px;
    }
  }
}

@media (max-width: 575px) {
  .burger {
    width: 50px;
    height: 50px;
    margin-left: auto;
    position: relative;

    &__span,
    &__span::before,
    &__span::after {
      width: 30px;
      height: 2px;
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -15px;
      margin-top: -1px;
      background-color: $black; 
    }

    &__span::before,
    &__span::after {
      content: '';
      transition: all 0.2s;
    }

    &__span::before {
      margin-top: -8px;
    }

    &__span::after {
      margin-top: 6px;
    }

      &--active {
        z-index: 3;
        position: fixed;
        top: 0;
        right: 0;

        .burger__span {
          background-color: transparent;

          &::before,
          &::after {
            margin-top: 0;
            background-color: $white;
          }

          &::before {
            transform: rotate(135deg);
          }

          &::after {
            transform: rotate(-135deg);
          }

        }

      }
  }

  .nav {
    opacity: 0.9;
    background-color: $blue;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s;
    top: -100%;

    &--active {

      top: 0;

      .menu__link,
      .menu__link:hover {
        color: $white
      }

    }
  }

  .menu {
    width: 100%;
  }

  .contacts {
    max-width: 100%;
    text-align: center;
    &__item {
      width: 50%;
    }
    &__link {
      &--phone {
        padding-left: 20px;
      }

      &--clock {
        padding-left: 23px;
      }

      &--address {
        padding-left: 20px;
      }
    }
  }

  .menu {
    display: block;
    text-align: center;

    &__link {
      display: block;
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
}