.contacts {

    &-form {
        background: url('../img/slide-img.jpg') center center no-repeat;
        background-size: cover;
        padding-top: 116px;
        padding-bottom: 139px;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.4);
        }
    }

    &-left {
        flex-basis: 42%;

        &__title {
            color: $white;
            font-family: Merriweather, sans-serif;
            font-weight: 900;
            font-size: 48px;
            line-height: 69px;
        }

        &__text {
            color: $white;
            font-family: Lato;
            font-weight: 300;
            font-size: 18px;
            line-height: 25px;
            letter-spacing: 1.1px;
            margin-bottom: 73px;
        }

        &__phone {
            font-weight: 300;
            font-size: 21px;
            line-height: 29px;
            color: $white;
            transition: color 0.5s;
            display: flex;
            align-items: center;

            &:hover {
                color: $blue;

                .contacts-left__phone-icon {
                    fill: $blue;
                }
            }

            &-icon {
                width: 16px;
                height: 34px;
                fill: $white;
                transition: fill 0.5s;
                margin-right: 17px;
            }
        }

        &__email {
            color: $white;
            font-weight: 300;
            font-size: 21px;
            line-height: 29px;
            transition: color 0.5s;
            display: flex;
            align-items: center;

            &:hover {
                color: $blue;
                .contacts-left__email-icon {
                    fill: $blue;
                }
            }

            &-icon {
                width: 37px;
                height: 32px;
                fill: $white;
                transition: fill 0.5s;     
                margin-right: 19px;        
            }
            
        }
    }

    &-right {
        flex-basis: 49%;

        &__first {

            .contacts-right__input {
                margin-right: 20px;
            }

        }

        &__label {
            color: $white;
            font-size: 16px;
            line-height: 22px;
            font-family: Lato;
            display: block;
            margin-bottom: 22px;
            position: relative;
            top: 16px;
        }

        &__input {
            border: none;
            border-bottom: 1px solid $white;
            background: none;
            outline: none;
            color: $white;
            padding-bottom: 10px;
            min-width: 264px;
            font-size: 21px;
            line-height: 29px;
        }

        &__textarea {
            border: none;
            border-bottom: 1px solid $white;
            background: none;
            outline: none;
            color: $white;
            min-height: 104px;
            width: 100%;
            resize: none;
        }

        &__form-wrapper {
            margin-bottom: 18px;
        }

        &__message {
            margin-bottom: 47px;
        }

        button {
            display: block;
            margin-left: auto;
        }

    }

}

@media (max-width: 1199px) {

    .contacts {

        &-left {

            &__wrapper {
                flex-direction: column;
            }

            &__phone {
                align-self: flex-start;
                margin-bottom: 20px;
            }

            &__email {
                align-self: flex-start;
            }

        }

    }

}

@media (max-width: 991px) {

    .contacts {

        &-form {
            padding-top: 50px;
            padding-bottom: 50px;
        }

        &-wrapper {
            display: block;
        }

        &-left {

            margin-bottom: 20px;

            &__title {
                font-size: 40px;
                margin-bottom: 30px;
                text-align: center;
            }

            &__text {
                margin-bottom: 20px;
                text-align: center;
            }

            &__wrapper {
                flex-direction: row;
                justify-content: center;
            }

            &__phone {
                margin-right: 30px;

                &-icon {
                    margin-right: 10px;
                }
            }

            &__email {

                &-icon {
                    margin-right: 10px;
                }

            }

        }

        &-right {

            &__form {
                max-width: 550px;
                margin: 0 auto;
            }

            &__form-wrapper {
                justify-content: flex-start;
            }

            &__textarea {
                width: auto;
                min-width: 545px;
            }

            .button {
                margin: 0 auto;
            }

        }

    }

}

@media (max-width: 575px) {

    .contacts {

        &-left {

            &__title {
                font-size: 22px;
            }

            &__wrapper {
                flex-direction: column;
                justify-content: center;
            }

            &__phone {
                margin-right: 0;
                align-self: center;
                font-size: 18px;

                &-icon {
                    width: 12px;
                    height: 23px;
                }
            }

            &__email {
                align-self: center;
                font-size: 18px;

                &-icon {
                    width: 24px;
                    height: 20px;
                }
            }

        }

        &-right {

            &__form-wrapper {
                justify-content: space-between;
            }

            &__first {
                flex-basis: 47%;
            }

            &__last {
                flex-basis: 47%;
            }

            &__input {
                min-width: auto;
                width: 100%;
                font-size: 18px;
            }

            &__textarea {
                min-width: auto;
                min-height: 80px;
                width: 100%;
            }

        }

    }

}