.promo {
    position: relative;
}

.slider {
    &__promo {
        position: relative;

        &--image-bg {
            min-height: 670px;
            background: url('../img/slide-img.jpg') center center no-repeat;
            background-size: cover;
        }
    }

    &__image {
        width: 100%;
    }

    &__content-promo {
        max-width: 590px;
        position: absolute;
        top: 23.7%;
        left: 20.6%;
    }
    &__title {
        color: $white;
        font-size: 48px;
        line-height: 66px;
        font-family: Merriweather, serif;
        margin-bottom: 67px;
        margin-top: 10px;
    }
}

.dots {
    &--promo {
        position: absolute;
        bottom: 109px;
        left: 20.6%;
        display: flex;
    }
    &__item {
        margin-right: 40px;       

        &:last-child {
            margin-right: 0;
        }        
    }

    &__dot {
        display: block;
        width: 10px;
        height: 10px;
        background-color: $white;
        border-radius: 50%;
        cursor: pointer;
        opacity: 0.4;
        transition: opacity 0.5s;

        &:hover {
            opacity: 1;
        }

        &--active {
            opacity: 1;
        }
    }
}

.arrows {
    &--promo {
        position: absolute;
        right: 21%;
        bottom: 108px;
        display: flex;
    }    

    &__item {
        position: relative;
        cursor: pointer;        

        &:hover {
            .arrows__button {
                opacity: 0.6;
            }
        }
    }

    &__button {
        display: block;
        width: 60px;
        height: 60px;
        background-color: #ffffff;
        opacity: 0.4;
        transition: opacity 0.5s;
    }

    &__arrow {
        display: block;
        width: 15px;
        height: 15px;
        border-left: 2px solid $white;
        border-bottom: 2px solid $white;
        position: absolute;
        top: 20px;

        &--left {
            transform: rotate(45deg);
            left: 25px;
        }

        &--right {
            transform: rotate(-140deg);
            right: 25px;
        }
    }
}

@media (max-width: 991px) {
    .slider {
        &__promo {

            &--image-bg {
                min-height: 450px;
            }
            
        }

        &__content-promo {
            top: 11%;
            max-width: 480px;
        }

        &__title {
            margin-bottom: 20px;
            font-size: 40px;
        }
    }
    .dots {
        &--promo {
            bottom: 75px;
        }
    }
    .arrows {
        &--promo {
            bottom: 75px;
        }
    }
}

@media (max-width: 767px) {
    .slider {
        &__content-promo {
            left: 10%;
        }
    }
    .dots {
        &--promo {
            left: 10%;
        }
    }
}

@media (max-width: 575px) {
    .slider {
        &__promo {

            &--image-bg {
                min-height: 340px;
            }
        }

        &__title {
            font-size: 30px;
            line-height: 45px;
        }

    }

    .dots {
        &--promo {
            bottom: 25px;
        }        

        &__item {
            margin-right: 20px;
        }
    }

    .arrows {
        &--promo {
            bottom: 25px;
        }

        &__button {
            width: 40px;
            height: 40px;
        }

        &__arrow {
            width: 10px;
            height: 10px;
            top: 13px;

            &--left {
                left: 15px;
            }

            &--right {
                right: 17px;
            }
        }
    }
}